select {
    appearance: none;
    background-repeat: no-repeat;
    padding: 10px;
    -webkit-padding-end: 30px;
    -webkit-padding-start: 10px;
    border-radius: 0px;
    border: 0;
    box-shadow: none;
    width: 100%;
    font-weight: bold;
    text-indent: 5px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    &::-ms-expand {
        display: none;
    }
    &:focus,
    &:active {
        outline: none;
    }
}

.pilihkota {
    text-transform: uppercase;
    letter-spacing: 1px;
}

.App {
    text-align: center;
}

.container {
    position: relative;
    max-width: 480px;
    margin: 0 auto;
}
  
.site-header,
.site-footer {
    background-color: #282c34;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.pt-wrapper {
    padding: 30px 0;
}
.tabel-heading {
    display: flex;
    justify-content: center;
    margin: 20px auto;
    max-width: 486px;
}
.innertext {
    margin: 0 20px;
    span {
        font-weight: bold;
    }
}

.headings {
    .head-row {
        display: flex;
        justify-content: space-between;
        padding: 8px;
        background-color: #c62d2d;
        color: #fff;
    }
}

.alldays {
    display: flex;
    flex-direction: column;
    .day {
        display: flex;
        justify-content: space-between;
        padding: 10px;
        border-left: 1px solid #eaeaea;
        border-right: 1px solid #eaeaea;
        &:nth-child(odd) {
            background-color: #ddd;
            &.today {
                background-color: #ff8e8e;
            }
        }
        &:nth-child(even) {
            background-color: #f9f9f9;
            &.today {
                background-color: #ff8e8e;
            }
        }
        &:last-child{
            border-bottom: 1px solid #eaeaea;
        }
    }
}